import React, { useEffect } from "react";
import { useState } from "react";
import { ThemeProvider } from "@mui/material/styles";
import { theme } from "../components/theme";
import { Button, TextField, Typography } from "@mui/material";

import "./style/crowdfunding/verifypayment.scss";

import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";


const VerifyPayment = () => {

  useEffect(() => {
    getData()
  }, [])

  const [inputValue, setinputValue] = useState([]);

  const [items, setItems] = useState([]);
  // const [userName, setuserName] = useState([]);
  // const [userAmount, setuserAmount] = useState([]);
  // const [userNumber, setuserNumber] = useState([]);
  // const [userStatus, setuserStatus] = useState([]);

  const [userArray, setuserArray] = useState([]);

 

  const getData = async () => {
    const data = await fetch(
      "https://sheets.googleapis.com/v4/spreadsheets/11A5yiDMBFjT8sWMBSTp0g1kaygAuDvxcY4cBQI1BblA/values/Sheet1?alt=json&key=AIzaSyAOhitHkJEz_XL4-eDBxaM_ndUaQjEE_8c"
    );

    const items = await data.json();
    setItems(items.values);
  };

  // console.log(userArray)

  function handleClick() {
    
    let arraylength = items.length;
    let userName = "";
    let userAmount = "";
    let userNumber = "";
    let userStatus = "";
    let userArray = []; 
    let count = 0
    let i = 0
    // console.log(userName)

    for (let index = 1; index < arraylength; index++) {
      if (items[index][2] === inputValue) {
             
        userName = items[index][1];
        userNumber = items[index][2];
        userAmount = items[index][3];
        userStatus = items[index][4];

        userArray[i] = [userName, userNumber, userAmount, userStatus] 
        // setuserArray(userArray);
        // setuserName(userNumber);
        // setuserName(userAmount);
        // setuserName(userStatus);
        i++
      }
      
      // else{
      //   userArray[0] = ['data not found', 'NA' , 'NA', 'NA']
      //   // setuserArray(userArray);
      // }
      setuserArray(userArray);
    }
   
  }

  // console.log(userArray)

  // console.log(userArray)

  // function createData(name, mobileNumber, amountInr, currentStatus, download) {
  //   return { name, mobileNumber, amountInr, currentStatus, download };
  // }
  
  // const rows = [createData(userArray[0], userArray[1], userArray[2], userArray[3])];
  

  return (
    <div>
      <ThemeProvider theme={theme}>
        <section id="section-one-verify">
          <div className="main-container">
            <div className="left">
              <Typography variant="h3" className="section-title">
                Verify Payment
              </Typography>
              <div className="underline"></div>
              <p className="section-info">
                Enter Mobile number / Unique Reference ID{" "}
              </p>
              <div className="text-field">
                <TextField
                  id="outlined-basic"
                  label="Enter Here"
                  variant="outlined"
                  onChange={(event) => {
                    setinputValue(event.target.value);
                  }}
                />
                <Button
                  onClick={handleClick}
                  variant="contained"
                  disableElevation
                >
                  Go
                </Button>
              </div>
            </div>
            <div className="right">
                <TableContainer className='table' component={Paper}>
                  <Table aria-label="simple table">
                    <TableHead>
                      <TableRow>
                        <TableCell>Name</TableCell>
                        <TableCell align="right">Mobile Number</TableCell>
                        <TableCell align="right">Amount INR</TableCell>
                        <TableCell align="right">Status</TableCell>
                        <TableCell align="right">Download</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {userArray.map((row) => (
                        <TableRow
                          key={row[2]}
                          sx={{
                            "&:last-child td, &:last-child th": { border: 0 },
                          }}
                        >
                          <TableCell component="th" scope="row">
                            {row[0]}
                          </TableCell>
                          <TableCell align="right">{row[1]}</TableCell>
                          <TableCell align="right">{row[2]}</TableCell>
                          <TableCell align="right">{row[3]}</TableCell>
                          {/* <TableCell align="right">{row.download}</TableCell> */}
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              </div>
          </div>
        </section>
      </ThemeProvider>
    </div>
  );
};

export default VerifyPayment;
